import React from "react";
import social from "../../images/children/social-about.webp";
import social2 from "../../images/children/social-program.webp";
import reception from "../../images/children/reception.webp";
import expression from "../../images/children/expression.webp";
import smileyBoy from "../../images/children/smiley-boy.png";
import arabicIcon from "../../images/advantages/arabic.svg";
import interactiveIcon from "../../images/advantages/interactive.svg";
import qualityIcon from "../../images/advantages/quality.svg";
import monitoringIcon from "../../images/advantages/monitoring.svg";
import mobilePreview from "../../images/misc/mobile-preview.png";
import googlePlay from "../../images/misc/google-play.png";

export default function Main() {
	return (
		<main>
			<About />
			<Programs />
			<Advantages />
			<AppPreview />
		</main>
	);
}

function About() {
	return (
		<div id="about">
			<div className="details">
				<h2>من نحن</h2>
				<h1>تطبيق أنيس</h1>
				<p>
					نحن فريق متخصص يعمل على تطوير تطبيق أنيس، وهو تطبيق يهتم بعلاج الأطفال ذوي اضطراب التوحد باستخدام
					الألعاب والذكاء الاصطناعي. يهدف تطبيقنا إلى تحسين تأخر اللغة ومهارات المواقف الاجتماعية للأطفال في فئة العمر من 4 إلى 7
					سنوات. نحن نؤمن بأن المنهجية المبتكرة المستندة إلى الألعاب تعد أفضل طريقة لتشجيع الأطفال وتحفيزهم على
					استخدام التطبيق وتحقيق تحسن ملحوظ. نحن نعمل جاهدين على توفير بيئة تعلم مشوقة ومحفزة للأطفال، ونضمن أن كل
					جانب في تصميم التطبيق متناسب وفقًا لاحتياجات الأطفال ذوي اضطراب التوحد. مع تطبيق أنيس، ستحصل على أداة
					فعالة وممتعة لدعم نمو وتطور طفلك المصاب بالتوحد في المجالات اللغوية والاجتماعية.
				</p>
				<button className="btn-register btn-pink">دخول</button>
			</div>
			<img src={social} />
		</div>
	);
}

function Programs() {
	return (
		<div id="programs">
			<h2>صديقك أنيس</h2>
			<h1>برامجنا</h1>
			<p>
				برامجنا متنوعة ومتخصصة لتلبية احتياجات الأطفال ذوي اضطراب التوحد. تشمل اللغة الاستقبالية، اللغة التعبيرية،
				والمواقف الاجتماعية. توفر هذه البرامج تدريبًا مكثفًا وممتعًا للأطفال لتحسين قدراتهم اللغوية والاجتماعية في
				جوانب الحياة العامة.
			</p>
			<div className="programs-container">
				<div className="program">
					<img src={reception} />
					<h2>اللغة الاستقبالية</h2>
					<p>
						تعزيز استيعاب الأطفال ذوي اضطراب التوحد وفهمهم للغة المحيطة عن طريق تعريضهم للكلمات الأساسية التي
						يحتاجون لها في الحياة اليومية.
					</p>
				</div>
				<div className="program">
					<img src={expression} />
					<h2>اللغة التعبيرية</h2>
					<p>
						تطوير مهارات التعبير اللغوي لدى الأطفال ذوي اضطراب التوحد من خلال عرض الكلمات والصور التي تمت تعلمها
						وطلب نطقها، مستخدمين نموذج الذكاء الاصطناعي للتحقق من صحة الإجابة.
					</p>
				</div>
				<div className="program">
					<img src={social2} />
					<h2>المواقف الاجتماعية</h2>
					<p>
						تعزيز قدرات الأطفال ذوي اضطراب التوحد في التفاعل الاجتماعي من خلال مشاهدة مقاطع فيديو للحياة اليومية
						وطلب التعرف على التفاصيل وتقليد الحوارات بصورة تفاعلية مع شخصية كرتونية ممتعة.
					</p>
				</div>
			</div>
		</div>
	);
}

function Advantages() {
	return (
		<div id="advantages">
			<div className="advantages-dummy-container">
				<img src={smileyBoy} />
				<div className="advantages-container">
					<h2>صديقك أنيس</h2>
					<h1>مميزاتنا</h1>
					<div className="advantages-details">
						<div className="advantage">
							<img src={arabicIcon} />
							<p>مدعم باللغة العربية</p>
						</div>
						<div className="advantage">
							<img src={interactiveIcon} />
							<p>تفاعلي</p>
						</div>
						<div className="advantage">
							<img src={qualityIcon} />
							<p>مبني على النهج العلمي المتبع في المراكز المتخصصة</p>
						</div>
						<div className="advantage">
							<img src={monitoringIcon} />
							<p>متابعة مستمرة للطفل</p>
						</div>
					</div>
				</div>
			</div>
		</div>
	);
}

function AppPreview() {
	return (
		<div id="app-preview">
			<div className="app-preview-details">
				<h1>تطبيق الجوال</h1>
				<p>
					تجربة مثيرة ومفيدة في متناول يديك! اكتشف التطبيق الرائع الذي يهدف إلى مساعدة طفلك المصاب بالتوحد على
					تحقيق تقدم مذهل في اللغة والمهارات الاجتماعية. استمتع بالتفاعل مع برامجنا الحصرية والمصممة خصيصًا لتشجيع
					الاستخدام المستمر والتعلم الممتع. ستجد في التطبيق مستويات متنوعة وألعاب مثيرة، واحصل على تقارير تقييمية
					لتتبع تقدم طفلك. قم بتحميل التطبيق الآن من جوجل بلاي وشاهد الفرق الواضح في قدرات طفلك!
				</p>
				<img src={googlePlay} alt="Get it on google play" />
			</div>
			<img src={mobilePreview} alt="Anees App" />
		</div>
	);
}
